<template>
  <div>
    <Card shadow>
      {{ $config.titleSuffix }}
      会自动将你程序中的错误收集起来，你可以将错误日志发给后端保存起来。
      在开发环境下，你程序中的错误都会被收集起来，这样不利于排查错误，可以将'src/config/index.js'的'catchErrorLog'设为false。
      如果只是想收集错误日志，不希望登录用户看到错误日志，可以关闭查看日志的入口，只需将'src/config/index.js'的'showLog'设为false。
    </Card>
    <Card shadow class="ivu-mt">
      <Row class="ivu-mt" :gutter="24">
        <Col span="6">
          <Button long @click="handleClick">点击测试触发程序错误</Button>
          <Button long @click="handleClick2" class="ivu-mt">测试read properties of undefined 错误</Button>
        </Col>
      </Row>
      <Row class="ivu-mt" :gutter="24">
        <Col span="6">
          <Button long @click="handleAxiosClick">点击测试触发axios接口请求错误</Button>
        </Col>
        <Col span="18">
          axios接口请求是请求easy-mock的一个不存在接口，所以服务端会报404错误，错误收集机制会收集这个错误，测试的时候有一定网络延迟，所以点击按钮之后稍等一会才会收集到错误。
        </Col>
      </Row>
    </Card>
  </div>
</template>

<script>
  import { errorReq } from '@/api/log'

  export default {
    name: 'CaptureLog',
    methods: {
      handleClick() {
        // eslint-disable-next-line no-undef
        console.log(admin)
      },
      handleClick2() {
        // eslint-disable-next-line no-use-before-define
        const admin = admin.account
        console.log(admin)
      },
      handleAxiosClick() {
        errorReq()
      }
    }
  }
</script>
<style lang="less" scoped>
  code {
    padding: 0.2em 0.4em;
    background-color: #fff5f5;
    border-radius: 3px;
    color: #fa795e;
  }
