<template>
  <PageView :content="content">
    <Card shadow>
      <Alert type="info" show-icon>
        用法说明
        <template #desc>打开控制台，点击不同的按钮，可显示不同的日志打印效果。</template>
      </Alert>
      <Row>
        <Col :sm="24" :md="24" :xl="24">
          <Divider>胶囊型</Divider>
          <Button @click="capsuleLog('default')">胶囊型（default）</Button>
          <Button type="primary" @click="capsuleLog('primary')">胶囊型（primary）</Button>
          <Button type="success" @click="capsuleLog('success')">胶囊型（success）</Button>
          <Button type="warning" @click="capsuleLog('warning')">胶囊型（warning）</Button>
          <Button type="error" @click="capsuleLog('error')">胶囊型（error）</Button>
          <Divider>普通型</Divider>
          <Button @click="commonLog('default')">普通型（default）</Button>
          <Button type="primary" @click="commonLog('primary')">普通型（primary）</Button>
          <Button type="success" @click="commonLog('success')">普通型（success）</Button>
          <Button type="warning" @click="commonLog('warning')">普通型（warning）</Button>
          <Button type="error" @click="commonLog('error')">普通型（error）</Button>
        </Col>
      </Row>
    </Card>
  </PageView>
</template>

<script>
  import { PageView } from '@/components'

  export default {
    name: 'ConsoleLog',
    components: { PageView },
    data() {
      return {
        content: '使用内置 $console 插件，可以在控制台打印各种日志。'
      }
    },
    methods: {
      capsuleLog(type) {
        this.$Message.info('请按F12打开控制台查看')
        this.$console.capsule(this.$config.titleSuffix, type, type)
      },
      commonLog(type) {
        this.$Message.info('请按F12打开控制台查看')
        this.$console[type](this.$config.titleSuffix, type)
      }
    }
  }
</script>

<style lang="less" scoped>
  button {
    margin: 0 15px 15px 0;
  }
</style>
